import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Skeleton from 'react-loading-skeleton';
import { getLoversProfiles } from '../../services/firebase';
import SuggestedProfile from './suggested-profile';

export default function LoversCount(userId, following, loggedInUserDocId, loving){
    const [loverCount, setLoverCount] = useState(null);
    useEffect(() =>{   
        async function suggestedProfiles() {
            const response = await getLoversProfiles(userId);
            setLoverCount(response);
        }
        if(userId.userId){
            suggestedProfiles();
        }

    }, [userId]); // Her UserID değiştiğinde tetikle

  // go ahead and get suggested profiles
  // hint: use the firebase service (call using userId)
  // getSuggestedProfiles
  // call the async function from the firebase service within useEffect
  // store it in state
  // go ahead and render (wait on the profiles as in 'skeleton')

    return !loverCount ? (
        <Skeleton count={10} height={150} className="mt-5"/> 
    ) : loverCount.length > 0 ? (
        <div classname="rounded flex flex-col">
            <div className="text-sm flex items-center align-items justify-between mb-2">
                <p className="font-bold text-gray-base">Sizden hoşlanan kişi sayısı</p>
            </div>
            <div className="my-2 grid gap-5">
                <p><b>{loverCount.length}</b></p>
            </div>
        </div>

    ) : null ;
}

LoversCount.propTypes = {
    userId: PropTypes.string,
    following: PropTypes.array,
    loggedInUserDocId: PropTypes.string
  };