import React from 'react';
import useUser from "../../hooks/use-user";
import User from './user';
import Suggestions from './suggestions';
import Lovers from './lovers';
import LoversCount from './lovercount';
import SavedProfiles from './savedProfiles';

export default function Sidebar() {
    const {
        user :  {docId, fullName, username, userId, following, loving, lovers, userPicture, rank}
    } = useUser(); 

    return (
    <div className="p-4">
        <User username={username} fullName={fullName} userPicture={userPicture} rank={rank}/>
        <Suggestions userId={userId} following={following} loggedInUserDocId={docId} loving={loving}/>
        <LoversCount userId={userId} following={following} loggedInUserDocId={docId} lovers={lovers}/>
        <Lovers userId={userId} following={following} loggedInUserDocId={docId} loving={loving}/>
        <SavedProfiles userId={userId} following={following} loggedInUserDocId={docId} loving={loving}/>
      </div>
    );
}