import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Skeleton from 'react-loading-skeleton';
import { getSuggestedProfiles } from '../../services/firebase';
import SuggestedProfile from './suggested-profile';


export default function Suggestions(userId, following, loggedInUserDocId, loving){
    const [profiles, setProfiles] = useState(null);
    useEffect(() =>{   
        async function suggestedProfiles() {
            const response = await getSuggestedProfiles(userId);
            setProfiles(response);
        }
        if(userId.userId){
            suggestedProfiles();
        }

    }, [userId]); // Her UserID değiştiğinde tetikle

  // go ahead and get suggested profiles
  // hint: use the firebase service (call using userId)
  // getSuggestedProfiles
  // call the async function from the firebase service within useEffect
  // store it in state
  // go ahead and render (wait on the profiles as in 'skeleton')

    return !profiles ? (
        <Skeleton count={10} height={150} className="mt-5" /> 
    ) : profiles.length > 0 ? (
        <div classname="rounded flex flex-col">
            <div className="text-sm flex items-center align-items justify-between mb-2">
                <p className="font-bold text-gray-base">Eşleşmeleriniz</p>
            </div>
            <div className="mt-4 grid gap-5 mb-5">
                {profiles.map((profile) => (
                <SuggestedProfile
                    key={profile.docId}
                    profileDocId={profile.docId}
                    username={profile.username}
                    profileId={profile.userId}
                    userId={userId.userId}
                    loggedInUserDocId={userId.loggedInUserDocId}
                    profilePicture={profile.userPicture}
                    rank={profile.rank}
                />
                ))}
            </div>
        </div>

    ) : null ;
}

Suggestions.propTypes = {
    userId: PropTypes.string,
    following: PropTypes.array,
    loggedInUserDocId: PropTypes.string
};