/* eslint-disable jsx-a11y/img-redundant-alt */
import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { getUserByUserUsername } from '../../services/firebase';

export default function Header({username, userPicture}){
    const [user, setUser] = useState('123');  

    useEffect(() => { // Eğer Kullanıcı değişirse
        async function getUserObjByUserId() {
            const result = await getUserByUserUsername(username);
            setUser(result);
        }
    
        if (username) {
          getUserObjByUserId();
        }

    }, []);

    return (
        <div className="flex border-b border-gray-primary h-4 p-4 py-8">
            <div className="flex items-center">
                <Link to={`/p/${username}`} className="flex items-center">
                    <img
                        className="rounded-full h-8 w-8 flex mr-3"
                        src={`${user[0].userPicture}`}
                        alt={`${username} profile picture`}
                    />
                    <p className="font-bold">{username}</p> {user[0].rank != "" &&(<p style={{color: "#6672DE"}} className="text-red text-left ml-1">{user[0].rank}</p>) }
                </Link>
            </div>
        </div>
    );
}

Header.propTypes = {
    username: PropTypes.string.isRequired
};