import { useState } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import {
    updateLoggedInUserFollowing,
    updateFollowedUserFollowers,
  } from '../../services/firebase';

export default function SuggestedProfile({profileDocId, username, profileId, userId, loggedInUserDocId, profilePicture, rank}){
    const [followed, setFollowed] = useState(false);

    async function handleFollowUser() {
        setFollowed(true);
        // Kullanıcının kendi hanesine takip edeceği kişinin ID'sini yaz
        await updateLoggedInUserFollowing(loggedInUserDocId, profileId, false);
        // Takip edilen kişinin takipçilerine kendi ID'ni yaz
        await updateFollowedUserFollowers(profileDocId, userId, false);
    }

    return !followed ? ( 
        <div>
            <div className="flex flex-row items-center align-items justify-between">
                <div className="flex items-center justify-between">
                    <img
                    className="rounded-full w-8 flex mr-3"
                    src={`${profilePicture}`}
                    alt=""
                    onError={(e) => {
                        e.target.src = `/images/avatars/karl.jpg`;
                    }}
                    />
                    <Link to={`/p/${username}`}>
                    <p className="font-bold text-sm">{username}</p>
                    </Link>
                    {!rank ? <img /> :<p className="ml-2 text-sm" style={{color: "#6672DE"}}>{rank}</p>}
                </div>
            </div>
        </div>
    ): null;
}


SuggestedProfile.propTypes = {
    profileDocId: PropTypes.string.isRequired,
    username: PropTypes.string.isRequired,
    profileId: PropTypes.string.isRequired,
    userId: PropTypes.string.isRequired,
    loggedInUserDocId: PropTypes.string.isRequired
  };